import { FirebaseStorageError } from './error';
import * as promiseimpl from './promise_external';
import * as RequestExports from './request';
import { Request } from './request';

/**
 * A request whose promise always fails.
 * @struct
 * @template T
 */
export class FailRequest implements Request {
 promise_: Promise;

 constructor(error: FirebaseStorageError) {
 this.promise_ = promiseimpl.reject(error);
 }

 /** @inheritDoc */
 getPromise() {
 return this.promise_;
 }

 /** @inheritDoc */
 cancel(appDelete = false) {}
}



